<template>
	<view class="func-wrap">
		<view v-if="!isFuncEdit" class="search-box">
			<view class="search-inner">
				<u-icon name="search" color="#909399" :size="28"></u-icon>
				<input class="search-text" type="text" v-model="searchInput"></input>
			</view>
		</view>

		<!-- <view class="nav-menu"> -->
		<chat-nav-menu :list="allFuncs" @updateAllListEvent="updateALlFuncList"></chat-nav-menu>
		<!-- </view> -->
	</view>
</template>

<script>
	import funcDataAll from '@/mock/func.data.all.js';
	import funcDataCommon from '@/mock/func.data.common.js';
	import {
		mapMutations,
		mapActions,
		mapState,
		mapGetters
	} from 'vuex';


	export default {
		data() {
			return {
				searchInput: '',
				commonFuncs: funcDataCommon[0].funcs,
				commonFuncList: [], //存放加工后的常用功能列表，比如添加是否展示角标isShowBadge
				commonFuncItem: {}, //用于向拖拽组件传新增项目
				allFuncs: [],
				allFuncList: [], //存放加工后的所有功能列表，比如添加是否展示角标isShowBadge
				isFuncEdit: false, //是否正在编辑常用功能
				gridBorder: false, //编辑的时候显示边框
			}
		},

		created() {
			console.log('created');
		},

		onShow() {
			console.log('onShow');
		},

		mounted() {
			console.log('mounted');
		},

		onReady() {
			console.log('onReady');
		},

		onLoad() {
			console.log('onLoad');
			let sourceCommonFuncList = funcDataCommon[0].funcs;

			for (let i = 0; i < sourceCommonFuncList.length; i++) {
				let itemObj = {
					isShowBadge: false
				};
				itemObj = {
					...sourceCommonFuncList[i],
					...itemObj,
				}
				//commonFuncList
				this.commonFuncList.push(itemObj)
			}
			// console.log('commonFuncList');
			// console.log(this.commonFuncList);
			this.commonFuncs = this.commonFuncList


			let sourceFuncList = funcDataAll;
			for (let i = 0; i < sourceFuncList.length; i++) {
				let sourceCateList = sourceFuncList[i].category
				let cateObj = new Object();
				let cateFuncList = []
				for (let j = 0; j < sourceCateList.length; j++) {
					let itemObj = {
						isShowBadge: false
					};
					itemObj = {
						...sourceCateList[j],
						...itemObj,
					}
					// console.log('itemObj:');
					// console.log(itemObj);
					cateFuncList.push(itemObj)
				}
				cateObj.name = sourceFuncList[i].name
				cateObj.category = cateFuncList
				this.allFuncList.push(cateObj)
			}

			this.allFuncs = this.allFuncList

		},

		onShow() {
			console.log('onShow');

		},


		computed: {
			
			//常用功能预览只显示6个图标
			preCommonFuncs() {
				return this.commonFuncs.slice(0, 6)
			}
		},

		watch: {
			searchInput() {
				let key = this.searchInput;
				let allFuncList = funcDataAll;
				let searchFuncList = []
				for (let i = 0; i < allFuncList.length; i++) {
					let categoryList = allFuncList[i].category
					let cateObj = new Object();
					let cateFuncList = []
					for (let j = 0; j < categoryList.length; j++) {
						let funcName = categoryList[j].name;
						if (categoryList[j].name.search(key) != -1) {
							cateFuncList.push(Object.create(categoryList[j]))
						}
					}
					if (cateFuncList.length > 0) {
						cateObj.name = allFuncList[i].name
						cateObj.category = cateFuncList
						searchFuncList.push(cateObj)
					}
				}
				this.allFuncs = searchFuncList
			}
		},


		methods: {

			//设置图片角标
			setImageBadge() {
				//处于编辑状态
				if (this.isFuncEdit) {
					//设置常用功能区角标
					this.commonFuncs.forEach((item) => {
						item.isShowBadge = true
					})
					this.allFuncs.forEach((cateItem) => {
						//最近使用功能区不设置角标
						if (cateItem.name == '最近使用') {
							let categorys = cateItem.category
							categorys.forEach((funcItem) => {
								funcItem.isShowBadge = false
							})
							//设置所有功能区角标
						} else {
							let categorys = cateItem.category
							categorys.forEach((funcItem) => {
								let funcName = funcItem.name
								//如果某个功能在常用功能区存在，则不设置角标
								if (this.isCommonFunc(funcName)) {
									funcItem.isShowBadge = false
									//如果某个功能在常用功能区不存在，则设置绿色加号角标
								} else {
									funcItem.isShowBadge = true
								}
							})
						}
					})
					//处于非编辑的状态
				} else {
					//设置常用功能区角标
					this.commonFuncs.forEach((item) => {
						item.isShowBadge = false
					})

					//设置所有功能区角标
					this.allFuncs.forEach((cateItem) => {
						let categorys = cateItem.category
						categorys.forEach((funcItem) => {
							funcItem.isShowBadge = false
						})
					})
				}

			},

			//判断某个功能是否是常用功能里的某一项
			isCommonFunc(funcName) {
				let flag = false
				this.commonFuncs.forEach(function(item) {
					if (funcName == item.name) {
						flag = true
					}
				})
				return flag
			},

			funcEdit() {
				this.isFuncEdit = true //设置当前处于编辑状态
				this.gridBorder = true //编辑状态添加边框
				//搜索功能会使allFuncs改变，重置赋值所有功能
				this.allFuncs = this.allFuncList
				this.setImageBadge()
			},



			//子组件cannetDragImage的emit事件会触发该方法
			updateCommonFuncList(list) {
				console.log('updateCommonFuncList');
				console.log(list);
				//更新commonFuncs
				this.commonFuncs = list
				//更新角标
				this.setImageBadge()

			},

			updateALlFuncList(item) {
				// addItem = {
				// 	...addItem,
				// 	...{
				// 		isShowBadge: true //addItem添加角标属性
				// 	}
				// }
				// //将新功能item传给组件cannetDragImage
				this.commonFuncItem = item
			},

			//取消编辑常用功能
			funcEditCancel() {
				this.isFuncEdit = false //设置当前不是编辑状态
				this.gridBorder = false //不是编辑状态去掉边框
				//设置所有功能区域角标不再显示
				this.allFuncs.forEach((cateItem) => {
					let categorys = cateItem.category
					categorys.forEach((funcItem) => {
						funcItem.isShowBadge = false
					})
				})
			},


			//提交编辑常用功能
			funcEditCommit() {
				this.isFuncEdit = false
				this.gridBorder = false

				this.allFuncs.forEach((cateItem) => {
					let categorys = cateItem.category
					categorys.forEach((funcItem) => {
						funcItem.isShowBadge = false
					})
				})
			},
		},
	}
</script>

<style lang="scss" scoped>
	.func-wrap {
		height: calc(100vh);
		/* #ifdef H5 */
		height: calc(100vh - var(--window-top));
		/* #endif */
		display: flex;
		flex-direction: column;


		.search-box {
			padding-left: 32rpx;
			padding-right: 32rpx;
			padding-top: 22rpx;
			padding-bottom: 22rpx;
			background-color: #4EB5B2;


			.search-inner {
				background-color: rgb(250, 250, 250);
				border-radius: 100rpx;
				display: flex;
				//justify-content: center;
				align-items: center;
				padding: 10rpx 16rpx;
			}

			.search-text {
				width: 100%;
				font-size: 26rpx;
				color: $u-tips-color;
				margin-left: 10rpx;
			}
		}

		.common-wrap {
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;
			height: 90rpx;
			background-color: #fff;

			.common-left {
				width: 160rpx;

				.common-title {
					font-size: 31rpx;
					font-weight: 900;
					text-align: center;
				}
			}

			.common-right {
				flex-grow: 1;
				display: flex;
				justify-content: space-between;
				align-items: center;

				.func-icons {
					display: flex;
					align-items: center;

					.func-icon-item {
						width: 62rpx;
						height: 62rpx;
						padding: 8rpx;
					}
				}

				.func-edit {
					padding-right: 36rpx;

					.func-edit-btn {
						width: 120rpx;
						line-height: 48rpx;
						font-size: 26rpx;
						border-radius: 28rpx;
						background: #4EB5B2;
						color: #fff;
					}

				}
			}
		}

		.common-edit-wrap {

			.common-edit-bar {
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 22rpx 32rpx;
				background-color: #4EB5B2;

				.common-edit-cancel {
					color: #FFF;
				}

				.common-edit-commit {
					color: #FFF;
				}
			}

			.common-edit-title {
				display: flex;
				align-items: center;
				padding: 16rpx 26rpx;
				background-color: #FFF;

				.common-edit-titleText {
					font-size: 32rpx;
				}

				.common-edit-subTitleText {
					padding-left: 22rpx;
					font-size: 22rpx;
				}
			}

			.common-edit-func-box {
				background-color: #FFF;
			}
		}

		.nav-menu {
			overflow: auto;
			height: 800rpx;

		}
	}
</style>
